/* App.css */

body {
  font-family: 'Press Start 2P', cursive; /* Cool gaming font */
  background-color: #121212; /* Dark background for neon theme */
  color: #fff; /* White text */
  margin: 0;
  display: flex;
  align-items: center;
  justify-content: center;
  min-height: 100vh;
}

.App {
  text-align: center;
  padding: 20px;
  border-radius: 8px;
  box-shadow: 0 0 20px rgba(255, 255, 255, 0.2); /* Light gray shadow */
  background: linear-gradient(45deg, #1a1a1a, #333333); /* Dark gradient background */
  width: 90%;
  max-width: 300px;
  margin: 0 auto;
  border: 4px solid #00ffcc; /* Neon border color */
  animation: borderAnimation 2s infinite alternate;
}

@keyframes borderAnimation {
  0% {
    border-color: #00ffcc; /* Neon border color on animation */
  }
  100% {
    border-color: #1a1a1a; /* Dark background border color on animation */
  }
}

button {
  font-size: 16px;
  padding: 15px 20px;
  margin: 10px 0;
  cursor: pointer;
  border: none;
  border-radius: 8px;
  background: linear-gradient(45deg, #1a1a1a, #333333); /* Dark gradient background */
  color: #fff; /* White text */
  transition: background-color 0.3s ease-in-out, transform 0.2s ease-in-out;
  transform: scale(1); /* Initial scale for dynamic effect */
}

button:hover {
  background: linear-gradient(45deg, #333333, #1a1a1a); /* Darker background on hover */
  transform: scale(1.05); /* Scale up on hover */
}

/* Compact table for numbers rolled */
.counts-container {
  margin-top: 10px; /* Reduced margin */
  display: flex;
  flex-wrap: wrap; /* Allow flex items to wrap to the next row */
  justify-content: space-between; /* Add space between rows */
}

/* Updated table style */
table {
  width: 48%; /* Adjusted width for two rows */
  border-collapse: collapse;
  text-align: left;
  margin-bottom: 10px; /* Space between two rows */
}

th, td {
  padding: 8px;
  border-bottom: 1px solid #fff; /* White border */
  font-size: 12px;
  color: #fff; /* White text */
}

/* Reset button style */
.reset-button {
  font-size: 16px;
  padding: 15px 20px;
  margin-top: 20px;
  cursor: pointer;
  border: 4px solid #fff; /* White border */
  border-radius: 8px;
  background: linear-gradient(45deg, #1a1a1a, #333333); /* Dark gradient background */
  color: #fff; /* White text */
  transition: background-color 0.3s ease-in-out, transform 0.2s ease-in-out;
  transform: scale(1); /* Initial scale for dynamic effect */
}

.reset-button:hover {
  background: linear-gradient(45deg, #333333, #1a1a1a); /* Darker background on hover */
  transform: scale(1.05); /* Scale up on hover */
}

/* Total rolls style without 3D effect */
.total-rolls {
  margin-top: 20px;
  font-size: 24px;
  color: #fff; /* White text */
  /* Removed transform property to untilt */
}

/* Last rolled style */
.last-rolled {
  margin-top: 10px;
  font-size: 16px;
  color: #fff; /* White text */
}

/* Heading style */
h1 {
  color: #00ffcc; /* Neon green color for casino theme */
  font-size: 24px;
  margin-bottom: 10px;
}
